#thanks .content {
  min-height: 150px !important;
  padding-top: 30px;
}

.thanks-content {
  .message {
    font-family: Arial, Verdana, sans-serif;
    background-color: #dff0d8;
    border: 1px solid transparent;
    border-color: #d6e9c6;
    border-radius: 5px;
    box-sizing: border-box;
    color: #3c763d;
    padding: 15px;
    text-align: center;

    h2 {
      font-weight: bold;
      font-size: 1.5em;
      margin-bottom: 15px;
    }

    p {
      line-height: 20px;
      padding: 0 20px;
    }
  }
}